import { prop, withType, flat, unique } from '../utils';
export default (function (families) {
  var connectors = [];
  families.filter(withType('parent')).forEach(function (family) {
    family.pUnits.forEach(function (pUnit) {
      var pX = family.left + pUnit.shift + pUnit.size;
      var pY = family.top + 1;
      var mY = family.top + 2;

      if (pUnit.size === 2) {
        connectors.push({
          points: [pX - 1, pY, pX - 1 + 2, pY]
        });
      }

      connectors.push({
        points: [pX, pY, pX, mY]
      });
      var ids = pUnit.nodes.map(prop('children')).reduce(flat).map(prop('id')).filter(unique);
      family.cUnits.forEach(function (cUnit) {
        var cIndex = cUnit.nodes.findIndex(function (node) {
          return ids.indexOf(node.id) !== -1;
        });
        var cX = family.left + cUnit.shift + cIndex * 2 + 1;
        connectors.push({
          points: [cX, mY, cX, mY + 1]
        });

        if (pX !== cX) {
          connectors.push({
            points: [Math.min(pX, cX), mY, Math.max(pX, cX), mY]
          });
        }
      });
    });
  });
  return connectors;
});