import { prop } from '../utils';
import Family from '../models/family';
import Unit from '../models/unit';
export default (function (store) {
  return function (childIDs) {
    var family = new Family(store.getNextId(), 'parent');
    var cUnit = new Unit(family.id, store.getNodes(childIDs), true);
    cUnit.nodes.forEach(function (child, cIndex) {
      var parents = store.getNodes(child.parents.map(prop('id'))).sort(function (a, b) {
        return b.gender !== store.gender ? -1 : 0;
      });

      if (parents.length) {
        var pUnit = new Unit(family.id, parents);
        pUnit.size > 1 && cUnit.size > 1 && store.gender === child.gender ? cUnit.shift += 2 : pUnit.shift = cUnit.shift + cIndex * 2;
        family.pUnits.push(pUnit);
      }
    });
    family.cUnits.push(cUnit);
    return family;
  };
});