import { withId, withType, inAscOrder } from '../utils';
export default (function (families) {
  var connectors = [];
  families.filter(withType('root')).forEach(function (family) {
    family.pUnits.forEach(function (pUnit) {
      var pX = family.left + pUnit.shift + 1;
      var pY = family.top + 1;

      if (pUnit.size === 2) {
        connectors.push({
          points: [pX, pY, pX + 2, pY]
        });
      } else if (pUnit.size === 1 && pUnit.nodes[0].spouses.length) {
        families.filter(function (rFamily) {
          return rFamily.id !== family.id;
        }).forEach(function (rFamily) {
          rFamily.pUnits.forEach(function (unit) {
            if (unit.nodes.findIndex(withId(pUnit.nodes[0].spouses[0].id)) !== -1) {
              var xX = [pX, rFamily.left + unit.shift + 1].sort(inAscOrder);
              connectors.push({
                points: [xX[0], pY, xX[1], pY]
              });
            }
          });
        });
      }
    });
  });
  return connectors;
});