import { prop, withType, min } from './index';
export default (function (store) {
  var families = store.familiesArray;
  var vShift = min(families.map(prop('top'))) * -1;
  if (vShift !== 0) families.forEach(function (family) {
    return family.top += vShift;
  });
  var rootChild = families.find(function (f) {
    return f.main;
  });
  var rootParent = families.find(function (f) {
    return f.cID === rootChild.id;
  });

  if (rootParent) {
    var cUnit = rootChild.pUnits[0];
    var pUnit = rootParent.cUnits[0];
    var diff = rootParent.left + pUnit.shift - (rootChild.left + cUnit.shift);
    if (diff > 0) families.filter(withType('child', 'root')).forEach(function (family) {
      return family.left += diff;
    });else if (diff < 0) families.filter(withType('parent')).forEach(function (family) {
      return family.left += diff * -1;
    });
    var hShift = min(families.map(prop('left')));
    if (hShift > 0) families.forEach(function (family) {
      return family.left -= hShift;
    });
  }

  return store;
});